import React from 'react';
import classNames from 'classnames';
import { SectionTilesProps } from '../../utils/SectionProps';
import SectionHeader from './partials/SectionHeader';
import Image from '../elements/Image';

const propTypes = {
  ...SectionTilesProps.types
}

const defaultProps = {
  ...SectionTilesProps.defaults
}
const TimingData = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {

  const outerClasses = classNames(
    'features-tiles section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'features-tiles-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const tilesClasses = classNames(
    'tiles-wrap',
    pushLeft && 'push-left'
  );

  const sectionHeader = {
    title: 'Lightning fast performance',
    paragraph: 'Applying multiple filters takes more time. Adjust the processing settings to speed up performance.'
  };

  const iconSize = 36;
  const iconPadding = "0.75rem";

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content pt-5" />
          <div className={tilesClasses}>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Example 1
                  </h4>
                  <p className="m-0 text-sm">
                    Settings for a 640x480 image
                    <ul>
                      <li>1 3x3 filter</li>
                      <li>Color</li>
                      <li>WebAssembly</li>
                      <li>Single-threaded</li>
                    </ul>
                    Time per frame: 65 milliseconds
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Example 2
                  </h4>
                  <p className="m-0 text-sm">
                    Settings for a 640x480 image
                    <ul>
                      <li>1 3x3 filter</li>
                      <li>Grayscale</li>
                      <li>WebAssembly</li>
                      <li>Single-threaded</li>
                    </ul>
                    Time per frame: 27 milliseconds
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Example 3
                  </h4>
                  <p className="m-0 text-sm">
                    Settings for a 640x480 image
                    <ul>
                      <li>5 3x3 filters, 1 5x5 filter</li>
                      <li>Grayscale</li>
                      <li>WebAssembly</li>
                      <li>Single-threaded</li>
                    </ul>
                    Time per frame: 170 milliseconds
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Example 4
                  </h4>
                  <p className="m-0 text-sm">
                    Settings for a 640x480 image
                    <ul>
                      <li>5 3x3 filters, 1 5x5 filter</li>
                      <li>Grayscale</li>
                      <li>WebAssembly</li>
                      <li>Multi-threaded</li>
                    </ul>
                    Time per frame: 59 milliseconds
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

TimingData.propTypes = propTypes;
TimingData.defaultProps = defaultProps;

export default TimingData;