import React, { useState } from 'react';
import classNames from 'classnames';
import { SectionProps } from '../../utils/SectionProps';
import ButtonGroup from '../elements/ButtonGroup';
import Button from '../elements/Button';
import Image from '../elements/Image';
import Modal from '../elements/Modal';

const propTypes = {
  ...SectionProps.types
}

const defaultProps = {
  ...SectionProps.defaults
}

const Downloads = React.forwardRef(({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}, ref) => {

  const outerClasses = classNames(
    'hero section center-content',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'hero-inner section-inner',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const iconSize = 24;

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container-sm">
        <div className={innerClasses}>
          <div className="hero-content">
            <h1 className="mt-0 mb-16 reveal-from-bottom" data-reveal-delay="200" >
              Downloads
            </h1>
            <div className="container-xs" ref={ref}>
              <p className="m-0 mb-32 reveal-from-bottom" data-reveal-delay="400">
                Download the application for your platform.
              </p>
              <div className="reveal-from-bottom" data-reveal-delay="600">
                <ButtonGroup>
                  <Button tag="a" color="primary" wideMobile href="">
                    <div className="mr-8">
                      <Image
                          src={require('./../../assets/images/icon-windows.svg')}
                          alt="Windows logo"
                          width={iconSize}
                          height={iconSize}
                          className="svg-white"
                        />
                    </div>
                    Windows
                  </Button>
                  <Button tag="a" color="primary" wideMobile href="">
                    <div className="mr-8 center-content">
                      <Image
                          src={require('./../../assets/images/icon-apple.svg')}
                          alt="Apple logo"
                          width={iconSize-4}
                          height={iconSize-4}
                          className="svg-white"
                        />
                    </div>
                    macOS
                  </Button>
                  <Button tag="a" color="primary" wideMobile href="">
                    <div className="mr-8">
                      <Image
                          src={require('./../../assets/images/icon-linux.svg')}
                          alt="Linux logo"
                          width={iconSize-2}
                          height={iconSize-2}
                          className="svg-white"
                        />
                    </div>
                    Linux
                  </Button>
                </ButtonGroup>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
});

Downloads.propTypes = propTypes;
Downloads.defaultProps = defaultProps;

export default Downloads;