import React from 'react';
import classNames from 'classnames';
import { SectionTilesProps } from '../../utils/SectionProps';
import SectionHeader from './partials/SectionHeader';
import Image from '../elements/Image';

const propTypes = {
  ...SectionTilesProps.types
}

const defaultProps = {
  ...SectionTilesProps.defaults
}
const FeaturesTiles = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  pushLeft,
  ...props
}) => {

  const outerClasses = classNames(
    'features-tiles section',
    topOuterDivider && 'has-top-divider',
    bottomOuterDivider && 'has-bottom-divider',
    hasBgColor && 'has-bg-color',
    invertColor && 'invert-color',
    className
  );

  const innerClasses = classNames(
    'features-tiles-inner section-inner pt-0',
    topDivider && 'has-top-divider',
    bottomDivider && 'has-bottom-divider'
  );

  const tilesClasses = classNames(
    'tiles-wrap center-content',
    pushLeft && 'push-left'
  );

  const sectionHeader = {
    title: 'Process images using a wide array of features',
    paragraph: 'Apply filters to your webcam in real time.'
  };

  const iconSize = 36;
  const iconPadding = "0.75rem";

  return (
    <section
      {...props}
      className={outerClasses}
    >
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={tilesClasses}>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: iconPadding }}>
                    <Image
                      src={require('./../../assets/images/icon-filter.svg')}
                      alt="Features tile icon 01"
                      width={iconSize}
                      height={iconSize}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Convolutional Filters
                  </h4>
                  <p className="m-0 text-sm">
                    Apply one or more filters to your webcam feed: sharpen, gaussian blur, and more.
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom" data-reveal-delay="200">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: iconPadding }}>
                    <Image
                      src={require('./../../assets/images/icon-color.svg')}
                      alt="Features tile icon 01"
                      width={iconSize}
                      height={iconSize}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Image Color
                  </h4>
                  <p className="m-0 text-sm">
                    Choose to view the image in color or grayscale. Grayscale images process faster than color images.
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom" data-reveal-delay="400">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: "0.5rem" }}>
                    <Image
                      src={require('./../../assets/images/icon-javascript.svg')}
                      alt="Features tile icon 01"
                      width={48}
                      height={48}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    JavaScript or WebAssembly
                  </h4>
                  <p className="m-0 text-sm">
                    Run the image processing using either JavaScript code or optimized WebAssembly code.
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: iconPadding }}>
                    <Image
                      src={require('./../../assets/images/icon-multithreading.svg')}
                      alt="Features tile icon 01"
                      width={iconSize}
                      height={iconSize}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Multithreading
                  </h4>
                  <p className="m-0 text-sm">
                    Use multiple of your CPU threads at once to speed up processing speed. Always leaves two threads free.
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom" data-reveal-delay="200">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: iconPadding }}>
                    <Image
                      src={require('./../../assets/images/icon-timing.svg')}
                      alt="Features tile icon 01"
                      width={iconSize}
                      height={iconSize}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Timing Analysis
                  </h4>
                  <p className="m-0 text-sm">
                    Keep track of how long each frame takes to process using a rolling average. More applied filters means more time needed per frame.
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom" data-reveal-delay="400">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: iconPadding }}>
                    <Image
                      src={require('./../../assets/images/icon-notes.svg')}
                      alt="Features tile icon 01"
                      width={iconSize}
                      height={iconSize}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Record Timing
                  </h4>
                  <p className="m-0 text-sm">
                    Capture processing time of 1,000 frames and download the output file.
                  </p>
                </div>
              </div>
            </div>

            <div className="tiles-item reveal-from-bottom" data-reveal-delay="400">
              <div className="tiles-item-inner">
                <div className="features-tiles-item-header">
                  <div className="features-tiles-item-image mb-16" style={{ padding: iconPadding }}>
                    <Image
                      src={require('./../../assets/images/icon-lightning.svg')}
                      alt="Features tile icon 01"
                      width={iconSize}
                      height={iconSize}
                    />
                  </div>
                </div>
                <div className="features-tiles-item-content">
                  <h4 className="mt-0 mb-8">
                    Lightning Fast Performance
                  </h4>
                  <p className="m-0 text-sm">
                    Process images with latency as low as 8 milliseconds per image.
                  </p>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
    </section>
  );
}

FeaturesTiles.propTypes = propTypes;
FeaturesTiles.defaultProps = defaultProps;

export default FeaturesTiles;