import React, { useRef } from 'react';
// import sections
import Hero from '../components/sections/Hero';
import FeaturesTiles from '../components/sections/FeaturesTiles';
import NativeFeatures from '../components/sections/NativeFeatures';
import TimingData from '../components/sections/TimingData';
import Downloads from '../components/sections/Downloads';

const Home = ({ ...props }) => {

  return (
    <>
      <Hero className="illustration-section-01" />
      <FeaturesTiles />
      <NativeFeatures invertMobile topDivider imageFill className="illustration-section-02" />
      <TimingData topDivider />
      <Downloads topDivider  {...props} />
    </>
  );
}

export default Home;